/* --- ANA SAYFA STİLLERİ --- */
.home-page {
    padding: 80px 1.5rem 100px 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
}

.section-title {
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--text-color);
    text-align: center;
}

/* Haftalık Seri Takibi */
.streak-tracker {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
}

.streak-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem; /* Reduced margin */
}

.streak-counter {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--secondary-color);
}

.streak-counter .fa-fire {
    animation: flame 1.5s infinite;
}

.streak-counter.animate .fa-fire {
    animation: burst 0.5s ease-out;
}

.pass-container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 1rem; /* Added margin */
}

#use-pass-btn {
    background: none;
    border: 1px solid var(--secondary-color);
    color: var(--secondary-color);
    padding: 0.3rem 0.6rem;
    border-radius: 8px;
    cursor: pointer;
    font-size: 0.8rem;
}

#pass-count-display {
    font-weight: 700;
}

.streak-circles {
    display: flex;
    justify-content: space-between;
    gap: 0.5rem;
}

.streak-circle {
    flex: 1;
    aspect-ratio: 1 / 1;
    border-radius: 50%;
    background-color: var(--card-background);
    border: 3px solid var(--border-color);
    transition: all 0.4s ease;
    cursor: pointer;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.streak-circle span {
    font-size: 0.8rem;
    font-weight: 700;
    color: var(--light-text-color);
    opacity: 0.5;
}

.streak-circle.completed {
    background-color: var(--primary-color);
    border-color: var(--primary-color);
    transform: scale(1.05);
    box-shadow: 0 4px 15px rgba(76, 175, 80, 0.4);
}

.streak-circle.passed {
    background-color: var(--secondary-color);
    border-color: var(--secondary-color);
    transform: scale(1.05);
    box-shadow: 0 4px 15px rgba(255, 152, 0, 0.4);
}

.streak-circle.completed span, .streak-circle.passed span {
    display: none; /* Hide day letter when completed/passed */
}

.streak-circle.passed::before, .streak-circle.completed::before {
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    color: white;
    font-size: 1rem;
    animation: popIn 0.3s ease-out;
}

.streak-circle.passed::before {
    content: '\f051'; /* fa-forward */
}

.streak-circle.completed::before {
    content: '\f00c'; /* fa-check */
}

/* ... (rest of the styles remain the same) ... */

@keyframes flame {
    0%, 100% { transform: scale(1) rotate(-2deg); }
    50% { transform: scale(1.1) rotate(2deg); }
}

@keyframes burst {
    0% { transform: scale(1); }
    50% { transform: scale(1.5); }
    100% { transform: scale(1); }
}
