.home-page {
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  padding: 80px 1.5rem 100px;
  display: flex;
}

.section-title {
  color: var(--text-color);
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
}

.streak-tracker {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}

.streak-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: .5rem;
  display: flex;
}

.streak-counter {
  color: var(--secondary-color);
  align-items: center;
  gap: .5rem;
  font-size: 1.2rem;
  font-weight: 700;
  display: flex;
}

.streak-counter .fa-fire {
  animation: 1.5s infinite flame;
}

.streak-counter.animate .fa-fire {
  animation: .5s ease-out burst;
}

.pass-container {
  align-items: center;
  gap: .5rem;
  margin-bottom: 1rem;
  display: flex;
}

#use-pass-btn {
  border: 1px solid var(--secondary-color);
  color: var(--secondary-color);
  cursor: pointer;
  background: none;
  border-radius: 8px;
  padding: .3rem .6rem;
  font-size: .8rem;
}

#pass-count-display {
  font-weight: 700;
}

.streak-circles {
  justify-content: space-between;
  gap: .5rem;
  display: flex;
}

.streak-circle {
  aspect-ratio: 1;
  background-color: var(--card-background);
  border: 3px solid var(--border-color);
  cursor: pointer;
  border-radius: 50%;
  flex: 1;
  justify-content: center;
  align-items: center;
  transition: all .4s;
  display: flex;
  position: relative;
}

.streak-circle span {
  color: var(--light-text-color);
  opacity: .5;
  font-size: .8rem;
  font-weight: 700;
}

.streak-circle.completed {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  transform: scale(1.05);
  box-shadow: 0 4px 15px #4caf5066;
}

.streak-circle.passed {
  background-color: var(--secondary-color);
  border-color: var(--secondary-color);
  transform: scale(1.05);
  box-shadow: 0 4px 15px #ff980066;
}

.streak-circle.completed span, .streak-circle.passed span {
  display: none;
}

.streak-circle.passed:before, .streak-circle.completed:before {
  color: #fff;
  font-family: "Font Awesome 5 Free";
  font-size: 1rem;
  font-weight: 900;
  animation: .3s ease-out popIn;
}

.streak-circle.passed:before {
  content: "";
}

.streak-circle.completed:before {
  content: "";
}

@keyframes flame {
  0%, 100% {
    transform: scale(1)rotate(-2deg);
  }

  50% {
    transform: scale(1.1)rotate(2deg);
  }
}

@keyframes burst {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.5);
  }

  100% {
    transform: scale(1);
  }
}
